* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "TT Norms";
}

.title {
font-size: 24px;
font-weight: 400;
line-height: 28px;
text-align: center;
position: relative;
    &::after {
        content:"";
        width: 60px;
        height: 1px;
        position: absolute;
        background-color: #A1C4FD;
        bottom: -23px;
        right: calc(50% - 30px);

    }
}